/** Manages form data and validation rules */
export class FormModel {
    constructor(events) {
        this.events = events;
        this.formErrors = {};
        this.payment = '';
        this.email = '';
        this.phone = '';
        this.address = '';
    }
    getPayment() {
        return this.payment;
    }
    getEmail() {
        return this.email;
    }
    getPhone() {
        return this.phone;
    }
    getAddress() {
        return this.address;
    }
    setPayment(value) {
        this.payment = value;
        // Emit payment change event
        this.events.emit('payment:changed', value);
        // Validate after setting payment
        this.validateOrder();
    }
    setEmail(value) {
        this.email = value;
    }
    setPhone(value) {
        this.phone = value;
    }
    setAddress(value) {
        this.address = value;
    }
    setOrderAddress(field, value) {
        if (field === 'address') {
            this.setAddress(value);
        }
    }
    setOrderData(field, value) {
        if (field === 'phone') {
            this.setPhone(value);
        }
        else if (field === 'email') {
            this.setEmail(value);
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.getAddress().trim()) {
            errors.address = '  ';
        }
        if (!this.getPayment()) {
            errors.payment = '  ';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:address', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    validateContacts() {
        const errors = {};
        if (!this.getEmail().trim()) {
            errors.email = '  email';
        }
        if (!this.getPhone().trim()) {
            errors.phone = '  ';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    getOrderLot(orderData) {
        return {
            payment: this.getPayment(),
            email: this.getEmail(),
            phone: this.getPhone(),
            address: this.getAddress(),
            total: orderData.total,
            items: orderData.items,
        };
    }
    resetForm() {
        this.setPayment('');
        this.setEmail('');
        this.setPhone('');
        this.setAddress('');
        this.formErrors = {};
    }
}
