import { BasePresenter } from '../base/presenter';
/** Handles contact form logic and validation */
export class ContactsPresenter extends BasePresenter {
    constructor(formModel, view, appState, api, events) {
        super(events);
        this.formModel = formModel;
        this.view = view;
        this.appState = appState;
        this.api = api;
        this.events.on('contacts:changeInput', this.handleInput.bind(this));
        this.events.on('contacts:submit', this.handleSubmit.bind(this));
        this.events.on('formErrors:change', this.handleFormErrors.bind(this));
    }
    handleInput({ field, value, }) {
        this.formModel.setOrderData(field, value);
        this.validateForm();
    }
    async handleSubmit() {
        if (this.formModel.validateContacts()) {
            const cart = this.appState.getCart();
            const orderData = {
                items: cart.items.map((item) => item.id),
                total: cart.total,
            };
            try {
                const response = await this.api.orderProducts(this.formModel.getOrderLot(orderData));
                if (response && response.id) {
                    this.appState.clearCart();
                    const successContent = document
                        .querySelector('#success')
                        .content.cloneNode(true);
                    const totalElement = successContent.querySelector('.order-success__description');
                    if (totalElement) {
                        totalElement.textContent = ` ${cart.total} `;
                    }
                    const successButton = successContent.querySelector('.order-success__close');
                    if (successButton) {
                        successButton.addEventListener('click', () => {
                            this.appState.closeModal();
                        });
                    }
                    this.appState.openModal(successContent, ' ');
                    this.view.resetForm();
                    this.formModel.resetForm();
                }
                else {
                    this.view.error = ' :   ';
                }
            }
            catch (error) {
                if (error instanceof Error) {
                    this.view.error = `: ${error.message}`;
                }
                else {
                    this.view.error = '    ';
                }
            }
        }
    }
    handleFormErrors(errors) {
        const errorMessage = Object.values(errors).join(', ');
        this.view.error = errorMessage;
    }
    validateForm() {
        const isValid = this.formModel.validateContacts();
        this.view.valid = isValid;
    }
}
